const Dps = (dpsList = []) => {
  const list = [...(dpsList || [])]
    const resList= []
    const guanchuanList= []
    const gongjiList= []

    list.forEach((item) => {
      if (item.countName) {
        resList.push(item)
      } else if (item.name.includes('')) {
        guanchuanList.push(item)
      } else if (item.name.includes('')) {
        gongjiList.push(item)
      } else {
        resList.push(item)
      }
    })
    if (guanchuanList?.length) {
      let number = 0
      let dps = 0

      guanchuanList.forEach((item) => {
        number = number + item?.number
        dps = dps + item?.dps
      })

      resList.push({
        name: '',
        number: number,
        dps: dps,
      })
    }

    if (gongjiList?.length) {
      let number = 0
      let dps = 0

      gongjiList.forEach((item) => {
        number = number + item?.number
        dps = dps + item?.dps
      })

      resList.push({
        name: '',
        number: number,
        dps: dps,
      })
    }

    resList.sort((a, b) => {
      return b.dps - a.dps
    })

    return resList.filter((item) => {
      return +item.dps > 0
    }).map(item => {
      return {
        name: item.name, 
        number: item.number,
        totalDps: item.dps,
        // dps: item.dps,
      }
    })
}

